
	/**
	 * vuex管理登陆状态，具体可以参考官方登陆模板示例
	 */
	import {
		mapMutations
	} from 'vuex';
	export default {
		methods: {
			...mapMutations(['login','addressListSave'])
		},
		onLaunch: function() {
			let userInfo = uni.getStorageSync('userInfo') || '';
			let addressList = uni.getStorageSync('addressList') || '';
			if(userInfo.id){
				//更新登陆状态
				uni.getStorage({
					key: 'userInfo',
					success: (res) => {
						this.login(res.data);
					}
				});
                uni.getStorage({
                    key: 'addressList',
                    success: (res) => {
                        this.addressListSave(res.data);
                    }
                });
			}
            if(addressList.id){
                uni.getStorage({
                    key: 'addressList',
                    success: (res) => {
                        this.addressListSave(res.data);
                    }
                });
            }
			
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		},
	}
